.header {
   position: absolute;
   left: 0;
   top: 0;
   width: 100%;

   @include high-tablet {
      padding-top: 20px;
   }

   &__container {
      @include flex-all-sb;

      @include high-tablet {
         align-items: center;
         justify-content: initial;
      }
   }

   &__phone {
      @include high-tablet {
         margin-left: auto;
         margin-right: 10px;
         font-size: 0;
         position: relative;
         width: 44px;
         height: 44px;
         border: 1px solid rgba(255, 255, 255, 0.08);
         border-radius: 10px;
         background-image: url('../img/phone.svg');
         background-position: center;
         background-size: 24px;
         background-repeat: no-repeat;
      }

      @include mobile {
         font-size: 0;
      }

   }
}